@font-face {
  font-family: 'Pixelated Display';
  src: url('./fonts/PixelatedDisplayRegular.eot');
  src: url('./fonts/PixelatedDisplayRegular.eot?#iefix') format('embedded-opentype'),
      url('./fonts/PixelatedDisplayRegular.woff2') format('woff2'),
      url('./fonts/PixelatedDisplayRegular.woff') format('woff'),
      url('./fonts/PixelatedDisplayRegular.ttf') format('truetype'),
      url('./fonts/PixelatedDisplayRegular.svg#PixelatedDisplayRegular') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1)100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


h1{
  font-family: 'Pixelated Display';
  font-weight: 200;
  color: #0ccac4;
}